.contacts {
  background-color: var(--block-color);
  padding-bottom: 100px;
  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }
  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__top {
    margin-bottom: 30px;
  }

  &__bottom {
    @include grid-2fr;
  }

  &__item {
    background-color: var(--light-color);
    padding: 30px;
  }

  &__name {
    margin-bottom: 24px;
  }

  &__buttons {
    margin-top: 30px;
    display: flex;
    column-gap: 16px;
  }

  .mail {
    a {
      color: var(--orange-color);
    }
  }

  .showmap {
    color: var(--orange-color);
    display: block;
    cursor: pointer;
  }
}
