.footer {
  background-color: var(--grey-color);

  .nav {
    padding-bottom: 48px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);

    &__item:not(:last-child)::before {
      background-color: rgba(255, 255, 255, 0.1);
      right: -73px;
    }
  }

  &__middle {
    padding-top: 56px;
    padding-bottom: 56px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);

    @include flex-all-sb;
  }

  &__button {
    padding: 10px 24px;
  }

  .social {
    @include flex-all-sb;
    column-gap: 8px;
  }

  &__links {
    @include flex-all-sb;
    padding-top: 48px;

    span {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--light-color);
    }

    a {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--light-color);
      text-decoration: underline;
      &:hover {
        color: var(--orange-color);
      }
    }
  }
}
