.searchloss {
  .searchsuccess__head {
    margin-bottom: 40px;
  }

  &__text1 {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: var(--grey-color);
    margin-bottom: 16px;
  }

  &__inner {
    @include flex-v-center;
    column-gap: 8px;
    margin-bottom: 16px;
  }

  &__text2 {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--grey-color);
  }

  .tel {
    a {
      color: var(--grey-color);
    }
  }

  &__text3 {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--grey-color);
  }
}
