.case {
  padding-bottom: 100px;

  &__head {
    @include flex-all-sb;
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    color: var(--dark-color);
    max-width: 900px;
  }

  &__content {
    display: flex;
    column-gap: 30px;
  }

  &__image {
    padding: 63px 76px;
    border: 1px solid var(--image-color);
    width: 570px;
    height: 470px;
  }

  &__right {}

  &__top {
    @include flex-v-center;
    column-gap: 64px;
  }

  &__line {
    @include flex-v-center;
    column-gap: 4px;

    span {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--num-color);
    }
  }

  &__name {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--grey-color);
  }


  &__check {
    @include flex-v-center;
    column-gap: 8px;
  }

  &_bottom {
    margin-bottom: 32px;
  }

  &__bottom {
    margin-top: 40px;
    display: flex;
    column-gap: 40px;
    margin-bottom: 114px;
  }



  &__buttons {
    display: flex;
    column-gap: 16px;
  }

  &__block {
    padding: 24px;
    background-color: var(--block-color);
    @include flex-v-center;
    column-gap: 50px;
  }

  .tel {
    a {
      color: var(--tel-color);
    }
  }

  .mail {
    a {
      color: var(--orange-color);
    }
  }
}

.amount {
  background-color: var(--light-color);
  border: 1px solid var(--border-color);
  padding: 10px 8px;
  @include flex-v-center;
  max-height: 48px;
  column-gap: 18px;

  &__svg {
    cursor: pointer;
  }

  &__num {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: var(--input-color);
  }
}
