.checkout {
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__content {
    @include flex-all-top;
    column-gap: 60px;
  }

  .modal4 {
    display: block;
    margin: 0;
    background: var(--block-color);
    padding: 40px;
    max-width: 555px;
  }

  .form {
    &__title {
      font-size: 24px;
      line-height: 32px;
      color: var(--dark-color);
      margin-bottom: 24px;
      max-width: 400px;
    }

    &__input {
      color: var(--text-color);
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;

    color: var(--grey-color);
    margin-bottom: 24px;
  }

  .case__block {
    margin-bottom: 0px;
    // max-width: 555px;
  }

  .tel {
    a {
      color: var(--tel-color);
    }
  }

  .mail {
    a {
      color: var(--orange-color);
    }
  }
}
