.block_pagination {
  @include flex-all-center;
  column-gap: 29px;

  P {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: var(--grey-color);
    cursor: pointer;
  }
  &__left {
    cursor: pointer;
  }
  &__right {
    cursor: pointer;
  }

  &__center {
    width: 30px;
    height: 30px;
    background-color: var(--orange-color);
    color: var(--light-color) !important;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
