.header2 {
  padding-top: 0;
  padding-bottom: 0;

  .block_search {
    background-color: var(--grey-color);
    display: block;
    padding: 0;
    padding-top: 21px;
    padding-bottom: 21px;
    max-width: 100%;

    &__inner {
      width: 100%;
    }

    &__name {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      white-space: nowrap;
      margin-right: 24px;
    }

    &__input {
      width: 100%;
    }
  }

  &__container {
    @include flex-all-sb;
  }

  .header__container {
    padding-top: 16px;
    padding-bottom: 16px;
  }
}
