.thank {
  @include bg-cover;
  height: 700px;
  text-align: center;

  .breadcrumbs {
    justify-content: center;
  }

  &__info {
    margin-top: 166px;
    margin-bottom: 24px;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: var(--light-color);
  }

  &__title {
    max-width: 622px;
    color: var(--light-color);
    margin: 0 auto;
    margin-bottom: 48px;
  }

  &__button {
    padding: 12px 31px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    display: block;
    max-width: 327px;
    margin: 0 auto;
  }
}
