.graph-modal__container {
  max-width: 600px;
  border-radius: 0px;
  margin: 100px auto;

  .custom-checkbox {
    display: none;
  }

  .custom-checkbox:checked+label::before {
    background-image: url(../img/svg/check_light.svg);
    background-size: 11px 8px;
  }

  .custom-checkbox+label::before {
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid var(--orange-color);
    background-color: var(--orange-color);
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
    margin-right: 16px;
    position: relative;
    top: 4px;
  }



  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  textarea:-webkit-autofill,
  textarea:-webkit-autofill:hover,
  textarea:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    transition: background-color 5000s ease-in-out 0s;
  }
}

.graph-modal__close {
  right: 8px;
  top: 8px;
  width: 24px;
  height: 24px;
  background-image: url(../img/svg/close.svg);
}

.input-file {
  position: relative;
  display: inline-block;
  margin-bottom: 16px;

  &::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/plus_big.svg);
    width: 24px;
    height: 24px;
    left: 24px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 1;
  }
}

.input-file-btn {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--grey-color);
  vertical-align: middle;
  text-align: center;
  border: 1px solid var(--border-color);
  border-radius: 4px;

  background-color: var(--light-color);
  padding: 12px 24px;
  padding-left: 58px;
  box-sizing: border-box;
}

.input-file-text {
  padding: 0 10px;
  line-height: 40px;
  display: inline-block;
}

.input-file input[type=file] {
  position: absolute;
  z-index: -1;
  opacity: 0;
  display: block;
  width: 0;
  height: 0;
}

textarea {
  resize: none;
}

//modal1 (connect_with_us)
////////////////////////
.modal1 {
  padding: 80px 60px 40px 60px;

  .form {
    &__title {
      margin-bottom: 40px;
    }

    &__label {
      display: block;

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }

    &__input {
      padding: 12px 20px;
      background-color: var(--light-color);
      border: 1px solid var(--border-color);
      width: 100%;
      outline: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--grey-color);
    }

    &__textarea {
      min-height: 160px;
    }

    &__button {
      padding: 12px 32px;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
    }

    &__check {
      margin-top: 24px;
      @include flex-v-center;
      height: 20px;
      margin-bottom: 24px;
    }

    &__text {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--grey-color);

      a {
        text-decoration: underline;
        color: var(--grey-color);
      }
    }
  }
}

