@media screen and (max-width: 576px) {
  .graph-modal__container {
    margin: 24px auto;
    padding: 40px 16px 40px 16px;

    .form__title {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 16px;
    }

    .form__input {
      font-size: 14px;
      line-height: 20px;
      padding: 14px 20px;
    }

    .form__text {
      font-size: 12px;
      line-height: 16px;
    }

    .form__block {
      flex-direction: column;
      row-gap: 8px;
      align-items: start;
      margin-bottom: 0;
    }

    .form__wrapper {
      padding: 40px 16px 16px 16px;
    }

    .form__inner {
      padding: 24px 16px;
    }

    .form__subtitle {
      font-size: 16px;
      line-height: 24px;
    }
  }

  .modal3 {
    padding: 0;
  }

  .modal6 {
    padding: 0;
  }

  .modal5 .form__inner {
    padding: 0;
  }

  .modal1 .form__inner {
    padding: 0;
  }

  .modal2 .form__inner {
    padding: 0;
  }
}

@media screen and (max-width: 500px) {
  .form__button {
    width: 100%;
  }
}

@media screen and (max-width: 375px) {
  .input-file {
    width: 100%;
  }

  .input-file-btn {
    width: 100%;
  }

  .modal3 {
    padding: 0;

    .input-file::before {
      left: 48px;
    }
  }
}
