.location {
  @include flex-v-start;
  column-gap: 8px;

  span {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);
  }
}

.tel {
  @include flex-v-center;
  column-gap: 8px;

  a {
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    color: var(--light-color);
    &:hover {
      color: var(--orange-color);
    }
  }
}

.mail {
  @include flex-v-center;
  column-gap: 8px;

  a {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);
    &:hover {
      color: var(--orange-color);
    }
  }
}

.mode {
  @include flex-v-center;
  column-gap: 8px;

  span {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--light-color);
  }
}


