.hero {

  &__slide {

    @include bg-cover;
    height: 700px;
  }

  &__title {
    margin-top: 158px;
    color: var(--light-color);
    max-width: 636px;
    margin-bottom: 32px;
  }

  &__subtitle {
    color: var(--light-color);
    margin-bottom: 64px;
    font-weight: 600;
    max-width: 800px;
  }
}

.special {
  background-color: var(--grey-color);
  text-align: center;
  padding-bottom: 421px;
  position: relative;

  &__title {
    color: var(--light-color);
    text-align: center;
    margin-bottom: 40px;
  }

  &__items {
    @include grid-6fr;
    margin-bottom: 40px;
  }

  &__item {
    background-color: var(--dark-color);
    border: 4px solid var(--dark-color);
    height: 384px;
    padding: 30px;

    a {
      margin-bottom: 20px;
      display: block;
    }

    &:hover {
      background: linear-gradient(0deg, rgba(30, 30, 30, 0.8), rgba(30, 30, 30, 0.8)), var(--grey-color);
      border: 4px solid var(--orange-color);
    }

    &:nth-child(1) {
      grid-column-start: 1;
      grid-column-end: 3;
    }

    &:nth-child(2) {
      grid-column-start: 3;
      grid-column-end: 5;
    }

    &:nth-child(3) {
      grid-column-start: 5;
      grid-column-end: 7;
    }

    &:nth-child(4) {
      grid-column-start: 1;
      grid-column-end: 4;
    }

    &:nth-child(5) {
      grid-column-start: 4;
      grid-column-end: 7;
    }
  }

  &__name {
    text-align: center;
    text-decoration-line: underline;
    color: var(--light-color);
    cursor: pointer;

    &:hover {
      color: var(--orange-color);
    }
  }

  &__bg_text {
    position: absolute;
    bottom: 48px;
    left: 0;
    width: 100%;
  }
}

.offers {
  background-color: var(--dark-color);
  padding-top: 0;

  &__video {
    margin-bottom: 80px;
    margin-top: -301px;
    position: relative;
    z-index: 1;

    a {
      display: block;
    }
  }

  &__image {
    min-height: 600px;
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &__title {
    color: var(--light-color);
    text-align: center;
    margin-bottom: 40px;
  }

  &__items {
    @include grid-4fr;
  }

  &__item {
    background-color: var(--grey-color);
    min-height: 312px;
    border-bottom: 1px solid var(--orange-color);

    &:hover {
      background-color: var(--orange-color);
    }
  }

  &__img_link {
    width: 270px;
    height: 200px;
    display: block;
    overflow: hidden;
  }

  &__img {
    width: 270px;
    height: 102%;
  }

  &__name {
    padding: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    text-decoration-line: underline;
    color: var(--light-color);
    cursor: pointer;
    display: block;
  }
}
