.nav {
  &__list {
    @include flex-all-sb;
    column-gap: 85.5px;
  }

  &__item {
    position: relative;

    &:not(:last-child)::before {
      position: absolute;
      right: -42px;
      top: 50%;
      transform: translateY(-50%);
      background: #403D3C;
      height: 16px;
      width: 1px;
      content: '';
    }

    a {
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;
      color: var(--light-color);
      &:hover {
        color: var(--orange-color);
      }
    }
  }

}
