.button_empty1 {
  border: 1px solid var(--orange-color);
  border-radius: 4px;
  padding: 10px 24px;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--light-color);
  display: inline-block;

  &:hover {
    background-color: var(--orange-color);
    color: var(--light-color);
  }
}

.button_empty2 {
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  color: var(--orange-color);
  padding: 10px 24px;
  border: 1px solid var(--border-color);
  border-radius: 4px;

  &:hover {
    border: 1px solid var(--orange-color);
    background-color: var(--orange-color);
    color: var(--light-color);
  }
}

.button_empty3 {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: var(--orange-color);
  padding: 16px 24px;
  padding-left: 84.25px;
  position: relative;
  border: 1px solid var(--border-color);

  &::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/download_dark.svg);
    width: 12px;
    height: 13px;
    left: 62px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }

  &:hover {
    background-color: var(--orange-color);
    border: 1px solid var(--orange-color);
    color: var(--light-color);

    &:before {
      background-image: url(../img/svg/download_light.svg);
    }
  }
}

.button_empty4 {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--grey-color);
  padding: 10px 15px;
  border: 1px solid var(--orange-color);
  border-radius: 4px;
  &:hover {
    background-color: var(--orange-color);
    color: var(--light-color);
  }
}

.button_empty5 {
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  color: var(--grey-color);
  padding: 10px 24px;
  border: 1px solid var(--border-color);
  border-radius: 4px;

  &:hover {
    border: 1px solid var(--orange-color);
    background-color: var(--orange-color);
    color: var(--light-color);
  }
}

.button1 {
  border: 1px solid var(--orange-color);
  background-color: var(--orange-color);
  color: var(--light-color);
  padding: 19px 48px;
  padding-right: 80px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  display: inline-block;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/arrow_right.svg);
    width: 16px;
    height: 12px;
    right: 52px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }

  &:hover {
    background-color: transparent;
  }
}

.button2 {
  border: 1px solid var(--orange-color);
  background-color: var(--orange-color);
  color: var(--light-color);
  padding: 19px 118px;
  padding-right: 151px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  display: inline-block;
  position: relative;
  border-radius: 4px;

  &::before {
    content: "";
    position: absolute;
    background-image: url(../img/svg/arrow_right.svg);
    width: 16px;
    height: 12px;
    right: 124px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
  }

  &:hover {
    background-color: transparent;
    color: var(--orange-color);

    &::before {
      background-image: url(../img/svg/arrow_right_orange.svg);
    }
  }
}

.button3 {
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: var(--grey-color);
  background-color: var(--light-color);
  border: 1px solid var(--light-color);
  border-radius: 4px;
  padding: 19px 48px;

  &:hover {
    color: var(--light-color);
    background-color: transparent;
  }
}

.button4 {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
