  }
}

.button5 {
  background-color: var(--orange-color);
  border: 1px solid var(--orange-color);
  border-radius: 4px;
  padding: 10px 57px;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: var(--light-color);

  &:hover {
