.cardinfo {
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__top {
    @include flex-v-start;
    column-gap: 30px;
  }

  &__content {
    max-width: 840px;
  }

  &__text {

    &:not(:last-child) {
      margin-bottom: 40px;
    }

    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--grey-color);

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }

  &__text2 {
    p {
      font-size: 24px;
      line-height: 32px;
    }
  }

  &__image {
    margin-bottom: 40px;
  }

  .offers__video {
    margin-top: 0;
    margin-bottom: 40px;
  }

  &__list {
    margin-bottom: 30px;
  }

  &__name {
    margin-bottom: 24px;
  }

  &__item {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--list-color);
    padding-left: 40px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      background-image: url(../img/svg/circle.svg);
      width: 24px;
      height: 24px;
      left: 0px;
      top: 2px;
      cursor: pointer;
    }

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__list2 {
    margin-left: 40px;
    margin-bottom: 12px;
  }

  &__item2 {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--list-color);
    padding-left: 20px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      background-image: url(../img/svg/circle_dark.svg);
      width: 4px;
      height: 4px;
      left: 0px;
      top: 10px;
      cursor: pointer;
    }

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__two {
    margin-bottom: 30px;
    @include flex-all-sb;
    column-gap: 30px;

    .cardinfo__text {
      margin-bottom: 0;
    }
  }





}

.cardinfo_aside {
  background-color: var(--grey-color);
  padding: 24px;
  max-width: 270px;

  &__info {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-color);
    margin-bottom: 8px;
  }

  &__name {
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    color: var(--light-color);
    margin-bottom: 16px;
  }

  &__button {
    padding: 12px 35px;
    margin-bottom: 16px;
  }

  &__text {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: var(--text-color);
  }

  .tel {
    a {
      font-weight: 700;
      font-size: 18px;
      line-height: 26px;
    }
  }
}

.mySwiper3 {
  margin-bottom: 30px;
}
