.header {
  background-color: var(--dark-color);
  border-bottom: 2px solid var(--orange-color);
  padding-top: 16px;
  padding-bottom: 16px;
  // position: relative;
  // z-index: 1001;

  &__container {
    @include flex-all-sb;
  }

  .logo {
    &__image {
      margin-bottom: 12px;
    }

    &__info {
      font-weight: 700;
      font-size: 10px;
      line-height: 14px;
      color: var(--light-color);
      text-align: center;
    }
  }

  &__contacts {
    @include flex-all-end;
    column-gap: 80px;
    margin-bottom: 16px;
  }





  &__basket {
    cursor: pointer;
  }

}
