.footer_svg {
  circle {
    stroke: var(--light-color);
  }

  path {
    fill: var(--light-color);
  }

  &:hover circle {
    stroke: var(--orange-color);
  }

  &:hover path {
    fill: var(--orange-color);
  }
}

.header_svg {
  path {
    fill: rgba(183, 177, 169);
  }

  &:hover path {
    fill: var(--light-color);
  }
}
