.basketempty {
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--grey-color);
    max-width: 580px;
    margin-bottom: 40px;

    a {
      font-weight: 400;
      font-size: 20px;
      line-height: 28px;
      color: var(--grey-color);
      text-decoration: underline;
      &:hover {
        color: var(--orange-color);
      }
    }
  }

  .block_search {
    background: var(--grey-color);
    padding: 20px;
    border-bottom: none;
    margin-bottom: 40px;
    column-gap: 16px;

    &__name {
      font-size: 18px;
      line-height: 26px;
      white-space: nowrap;
    }
    &__inner {
      width: 100%;
    }

    &__input {
      width: 100%;
      padding: 13px 20px;
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__text2 {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--grey-color);
    margin-bottom: 40px;
  }

  .case__block {
    margin-bottom: 40px;
    max-width: 570px;
  }

  .tel {
    a {
      color: var(--tel-color);
    }
  }

  .mail {
    a {
      color: var(--orange-color);
    }
  }
}
