.hero_slider {
  position: relative;

  .swiper-pagination {
    display: flex;
    column-gap: 40px;
    justify-content: center;
    bottom: 30px;
  }

  .swiper-pagination-bullet {
    background-color: var(--light-color);
    opacity: 1;
  }

  .swiper-pagination-bullet-active {
    background-color: var(--orange-color);
  }

  .swiper-button-next:after,
  .swiper-button-prev:after {
    display: none;
  }

  .swiper-button-prev {
    left: 150px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    width: 60px;
    height: 60px;
    content: '';
    position: absolute;
  }

  .swiper-button-next {
    right: 150px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    width: 60px;
    height: 60px;
    content: '';
    position: absolute;
  }
}

