.mySwiper5_wrapper {
  background-color: var(--border-color);
  padding: 30px 54px;
  margin-bottom: 30px;
  position: relative;

  .swiper-button-next:after,
  .swiper-button-prev:after {
    display: none;
  }

  .swiper-button-prev {
    background-image: url('../img/svg/arrow_left_dark.svg');
    @include bg-cover;
    width: 24px;
    height: 24px;
  }

  .swiper-button-next {
    background-image: url('../img/svg/arrow_right_dark.svg');
    @include bg-cover;
    width: 24px;
    height: 24px;
  }
}

.mySwiper5 {

  .swiper-slide {
    background-color: var(--light-color);
    padding: 16px;
    width: 205.33px !important;
    height: 224px;
  }

  &__link_img {
    display: block;
    margin-bottom: 16px;
  }

  &__image {}

  &__name {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: var(--tel-color);
  }



}


