.error {
  @include bg-cover;
  height: 700px;

  &__title {
    margin-top: 166px;
    margin-bottom: 24px;
    color: var(--light-color);
  }

  &__info {
    margin-bottom: 48px;

    p {
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      color: var(--light-color);

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }

  &__button {
    padding: 12px 31px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    display: block;
    max-width: 328px;
  }
}
