.searchsuccess {
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }

  &__title {
    margin-top: 40px;
    margin-bottom: 60px;
  }

  &__head {
    @include flex-all-sb;
    column-gap: 16px;
    margin-bottom: 60px;

    .form__input {
      padding: 12px 20px;
      background-color: var(--light-color);
      border: 1px solid var(--border-color);
      width: 100%;
      outline: none;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--grey-color);
    }
  }

  &__button {
    font-size: 16px;
    line-height: 24px;
    padding: 12px 32px;
  }

  &__items {
    margin-bottom: 40px;
  }

  &__item {
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 40px;
    @include flex-v-center;

    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  &__num {
    margin-right: 52px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--grey-color);
  }

  &__image {
    margin-right: 40px;
    width: 170px;
    height: 170px;
  }

  &__inner {
    display: flex;
  }

  &__name {
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    text-decoration-line: underline;
    color: var(--grey-color);
    margin-bottom: 16px;
    display: block;
    &:hover {
      color: var(--orange-color);
    }
  }

  &__text {
    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--grey-color);

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
}
