.breadcrumbs {
  @include flex-v-center;
  column-gap: 9px;
  padding-top: 16px;

  p {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    text-decoration-line: underline;
    color: var(--orange-color);

    &:not(:last-child) {
      cursor: pointer;
    }

    &:last-child {
      color: var(--light-color);
      text-decoration: none;
    }
  }
}
