.policy {
  padding-bottom: 100px;

  &__head {
    margin-top: 56px;
    margin-bottom: 40px;
    @include flex-all-sb;
  }

  &__title {}

  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }

  &__name {
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
    color: var(--grey-color);
    margin-bottom: 20px;
  }

  &__text {

    &:not(:last-child) {
      margin-bottom: 48px;
    }

    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--grey-color);

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
}
