.card {
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }

  &__title {
    max-width: 623px;
  }

  &__head {
    margin-top: 56px;
    margin-bottom: 40px;
    @include flex-all-sb;
  }

  &__top {
    display: flex;
    @include flex-h-sb;
    column-gap: 30px;
    margin-bottom: 100px;
  }

  &__slider {
    display: flex;
    column-gap: 8px;
    max-width: 670px;
  }

  &__info {}

  &__buttons {
    @include flex-all-sb;
    column-gap: 8px;
    padding-top: 56px;
    margin-bottom: 40px;
  }

  &__button {}

  .btn1 {
    padding: 16px 59px;
    padding-left: 84px;

    &::before {
      left: 23px;
    }
  }

  .btn2 {
    padding: 16px 20px;
    padding-left: 45px;

    &::before {
      left: 23px;
    }
  }

  .btn3 {
    margin-bottom: 40px;
  }

  &__block {
    background-color: var(--block-color);
    padding: 24px;
    width: 100%;
  }

  .tel {
    margin-bottom: 16px;

    a {
      font-weight: 700;
      color: var(--tel-color);
    }

    span {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: var(--tel-color);

    }
  }

  .mail {
    a {
      font-size: 16px;
      line-height: 24px;
      color: var(--orange-color);
    }
  }

  &__subtitle {
    margin-bottom: 30px;
  }

  &__items {
    @include grid-3fr;
  }

  &__item {
    background-color: var(--block-color);
    padding: 24px;
    min-height: 120px;
  }

  &__name {
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    color: var(--grey-color);
    margin-bottom: 24px;
  }

  &__li {
    position: relative;
    padding-left: 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--grey-color);

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &::before {
      position: absolute;
      content: '';
      width: 4px;
      height: 4px;
      background-color: var(--grey-color);
      border-radius: 50%;
      left: 0;
      top: 6px;
    }
  }
}

.info {
  background-color: var(--orange-color);

  &__container {
    @include flex-all-sb;
    column-gap: 78px;
  }

  &__subtitle {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    margin-bottom: 8px;
  }

  &__title {
    color: var(--light-color);
    max-width: 472px;
  }

  &__right {
    text-align: left;
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    margin-bottom: 8px;
  }

  &__tel {
    font-weight: 700;
    font-size: 30px;
    line-height: 40px;
    color: var(--light-color);
  }
}
