.category {
  padding-top: 80px;
  padding-bottom: 100px;

  &__container {
    @include flex-h-sb;
    column-gap: 50px;
  }

  &__content {
    max-width: 900px;
  }

  .block_search {
    background-color: var(--grey-color);
    border-bottom: none;
    padding: 20px;
    column-gap: 16px;
    margin-bottom: 30px;


    &__name {
      font-size: 18px;
      line-height: 26px;
    }

    &__input {
      width: 316px;
      padding: 13px 20px;
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__items {
    @include grid-4fr-20;
    margin-bottom: 30px;
  }

  &__item {
    padding: 16px;
    border: 1px solid var(--border-color);
    min-height: 224px;
    text-align: center;

    a {
      display: block;
      margin-bottom: 16px;
    }
  }

  &__name {
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    text-decoration-line: underline;
    color: var(--grey-color);

    &:hover {
      color: var(--orange-color);
    }
  }
}
