.subcategory {
  margin-bottom: 30px;

  &__items {}

  &__item {
    background-color: var(--light-color);
    border: 1px solid var(--border-color);
    padding: 16px;
    @include flex-all-top;
    column-gap: 10px;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__img {
    margin-right: 24px;
  }

  &__info {
    // margin-right: 40px;
  }

  &__name {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    color: var(--grey-color);
    margin-bottom: 8px;
    display: block;
    max-width: 385px;

    &:hover {
      color: var(--orange-color);
    }
  }

  &__subname {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--grey-color);
    margin-bottom: 8px;
    max-width: 385px;
  }

  &__bottom {
    @include flex-v-center;
    column-gap: 40px;
  }

  &__num {
    @include flex-v-center;
    column-gap: 4px;

    span {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: var(--num-color);
    }
  }

  &__count {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: var(--grey-color);
  }

  &__check {
    @include flex-v-center;
    column-gap: 4px;

    span {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: var(--grey-color);
    }
  }

  &__svg {
    cursor: pointer;
  }

  &__buttons {
    max-width: 187px;
  }

  &__button {
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }


}

.block_pagination {
  margin-bottom: 70px;
}

.accordion {
  &__inner {
    li {
      padding-top: 6px;
      padding-bottom: 6px;
      padding-left: 24px;

      a {
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: var(--grey-color);
      }
      &:hover {
        background-color: var(--orange-color);
      }
    }
  }
}
