.burger {
  @include burger
}

.header .nav {
  display: none;
}

.header .header__info .nav {
  display: flex;
}

.burger {
  display: none;
}

.nav.menu--active {
  margin-top: 121px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 2;
  display: block;
  background-color: var(--grey-color);
}

.nav_burger {
  .nav__list {
    flex-direction: column;

  }

  .nav__item {
    padding-top: 16px;
    padding-bottom: 16px;
    border-bottom: 1px solid var(--burger-color);
    width: 100%;
    padding-left: 26px;
    padding-right: 26px;
  }

  .tel {
    justify-content: center;
    margin-bottom: 16px;
  }

  .nav__button {
    margin: 0 auto;
    display: block;
    margin-bottom: 16px;
  }

  .mail {
    justify-content: center;
  }

}

.posrel_max {
  position: relative;
  z-index: 1001;
}

.graph-modal {
  background: rgba(0, 0, 0, 0.5);
}

@media screen and (max-width: 930px) {
  .burger {
    display: block;
  }
}

@media screen and (max-width: 650px) {
  .nav.menu--active {
    margin-top: 84px;
  }
}

@media screen and (max-width: 576px) {
  .nav_burger {
    .nav__item {
      padding-left: 16px;
      padding-right: 16px;

      a {
        font-size: 16px;
        line-height: 24px;
      }
    }
  }
}
