.directory_preview {
  @include bg-cover;
  height: 305px;

  .container {
    position: relative;
  }

  &__head {
    margin-top: 88px;
    @include flex-v-center;
    column-gap: 40px;
  }

  &__title {
    color: var(--light-color);
  }

}

.block_search {
  background-color: var(--dark-color);
  padding: 30px;
  border-bottom: 2px solid var(--orange-color);
  @include flex-all-sb;
  bottom: -53px;
  width: 100%;
  max-width: 1170px;

  &__name {
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    color: var(--light-color);
  }

  &__inner {
    position: relative;

    &::before {
      background-image: url(../img/svg/search_dark.svg);
      cursor: text;
      content: '';
      position: absolute;
      right: 22px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
    }

    &:not(:last-child) {
      margin-right: 16px;margin-left: auto;
    }
  }

  &__input {
    padding: 11px 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    border: 1px solid var(--border-color);
    color: var(--input-color);
    width: 432px;
    outline: none;
  }
}

.directory {
  padding-top: 133px;
  padding-bottom: 100px;

  &__items {
    @include grid-6fr-20;
    margin-bottom: 100px;
  }

  &__item {
    padding: 16px;
    min-height: 208px;
    background-color: var(--block-color);
    border-bottom: 2px solid var(--orange-color);
  }

  a {
    margin-bottom: 8px;
    display: block;
  }

  &__name {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    text-decoration-line: underline;
    color: var(--dark-color);

    &:hover {
      color: var(--orange-color);
      text-decoration: none;
    }
  }
}

.text_close {
  max-height: 216px;
  overflow: hidden;
}

.text_open {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  margin-top: 40px;
  color: var(--orange-color);
  cursor: pointer;
  text-decoration: underline;
  text-decoration-style: dotted;
}

.text {
  p {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
}
