.basket {
  padding-bottom: 100px;

  .breadcrumbs {
    p {
      &:last-child {
        color: var(--grey-color);
      }
    }
  }

  &__title {
    margin-top: 56px;
    margin-bottom: 40px;
  }

  &__content {
    max-width: 870px;
  }

  &__top {
    @include flex-all-sb;
    padding: 8px 16px;
    background-color: var(--block-color);
    border: 1px solid var(--border-color);
    margin-bottom: 16px;
  }

  &__part {
    @include flex-all-sb;
    column-gap: 8px;
    cursor: pointer;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--input-color);
    cursor: pointer;
  }

  &__head {
    @include flex-all-sb;
    border: 1px solid var(--border-color);
    background-color: var(--light-color);
    padding: 16px;
  }

  &__items {
    margin-bottom: 40px;
  }

  &__item {
    @include flex-all-sb;
    padding: 16px;
    border: 1px solid var(--border-color);

  }

  &__checkbox {
    margin-right: 24px;
  }

  &__image {
    margin-right: 24px;
  }

  &__inner {
    display: flex;
  }

  &__middle {
    margin-right: 40px;
  }

  &__name {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    color: var(--grey-color);
    margin-bottom: 8px;
    display: block;
  }

  &__info {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--grey-color);
    max-width: 540px;
    margin-bottom: 8px;
  }

  &__num {
    display: flex;
    column-gap: 8px;

    p {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: var(--num-color);
    }

    span {
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;
      color: var(--grey-color);
    }
  }

  .amount {
    margin-bottom: auto;
  }

  &__button {
    padding: 15px 48px;
    padding-right: 80px;

    &:before {
      right: 52px;
    }
  }
}
