//     sass- -  root
// colors
:root {
  // base
  --font-family: "NotoSans", sans-serif;
  --content-width: 1170px;
  --container-offset: 26px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --dark-color: #1E1E1E;
  --orange-color: #E35B0A;
  --grey-color: #403D3C;
  --text-color: #B7B1A9;
  --border-color: #E6E6E6;
  --image-color: #D9D9D9;
  --block-color: #F5F5F5;
  --tel-color: #333333;
  --input-color: #9D9892;
  --num-color: #999999;
  --list-color: #3B3B36;
  --burger-color: #5C5958;
}
