.catalog_preview {
  background-image: url(../img/jpg/bg_catalog.jpg);
  @include bg-cover;
  height: 404px;

  &__title {
    color: var(--light-color);
    margin-top: 40px;
    margin-bottom: 40px;
  }

  &__items {
    @include grid-5fr;
  }

  &__item {
    height: 232px;
    background-color: rgba(30, 30, 30, 0.8);
    border-radius: 4px 4px 0px 0px;
    padding: 16px;
    text-align: center;
    border: 1px solid transparent;

    &:hover {
      border: 1px solid var(--orange-color);
    }
  }

  &__image {
    margin-bottom: 8px;
  }

  &__name {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    text-decoration-line: underline;
    color: #B7B1A9;

    &:hover {
      color: var(--light-color);
      cursor: pointer;
    }
  }
}

.catalog {
  padding-bottom: 100px;

  &__title {
    margin-bottom: 24px;
  }

  &__items {
    @include grid-4fr-20;

    &:not(:last-child) {
      margin-bottom: 60px;
    }
  }

  &__item {
    padding: 16px;
    min-height: 364px;
    border: 1px solid var(--border-color);

    &:hover {
      border: 1px solid var(--orange-color);
    }
  }

  &__preview_link {
    margin-bottom: 16px;
    width: 245.5px;
    height: 180px;
    display: block;
  }

  &__preview {
    object-fit: cover;
    height: 100%;
    width: 100%;
  }

  &__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-decoration-line: underline;
    color: var(--grey-color);
    margin-bottom: 16px;
    display: block;
  }

  &__info {
    @include flex-v-start;
    column-gap: 10px;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &__dopinfo {
    margin-top: 100px;

    p {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: var(--grey-color);

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--grey-color);
  }
}
