.about_preview {
  @include bg-cover;
  height: 400px;
  text-align: center;

  &__container {
    text-align: center;
  }

  .breadcrumbs {
    justify-content: center;
  }

  &__title {
    color: var(--light-color);
    margin-top: 117px;
    margin-bottom: 24px;
  }

  &__subtitle {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: var(--light-color);
    max-width: 970px;
    margin: 0 auto;
  }
}

.about {

  &__sertifikat {
    padding-left: 100px;
    padding-right: 100px;
    @include flex-all-sb;
    margin-bottom: 100px;
    column-gap: 60px;
  }

  &__text {
    max-width: 640px;

    p {
      font-weight: 400;
      font-size: 18px;
      line-height: 26px;
      color: var(--grey-color);

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }

  &__image {
    border: 8px solid var(--dark-color);
    min-width: 270px;
    height: 356.84px;
    cursor: pointer;
  }

  &__items {
    @include grid-3fr;
  }

  &__item {
    min-height: 288px;
    background-color: var(--border-color);
  }

  &__head {
    background-color: var(--dark-color);
    padding: 16px 50px;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: var(--light-color);
  }

  &__body {
    padding: 24px 24px 0px 24px;
  }

  &_fix {
    &:not(:last-child) {
      margin-bottom: 16px;
    }

    p {
      font-weight: 700;
      font-size: 14px;
      line-height: 20px;
      color: var(--grey-color);
    }

    span {
      color: var(--grey-color);
    }

    a {
      color: var(--grey-color);
    }
  }

  .mail {
    a {
      color: var(--orange-color);
    }
  }
}
