.service_preview {
  @include bg-cover;
  height: 600px;
  text-align: center;

  .breadcrumbs {
    justify-content: center;
  }

  &__title {
    margin-top: 138px;
    color: var(--light-color);
    margin-bottom: 24px;
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    color: var(--light-color);
    margin-bottom: 64px;
  }

  &__button {
    font-weight: 600;
  }
}

.text {

  &__container {
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--grey-color);

    p {
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
}

.map {
  .info {
    text-align: center;
    padding: 90px 0px;
    position: absolute;
    bottom: -250px;
    left: 0;
    right: 0;
    max-width: 1170px;

    &__title {
      max-width: 100%;
      font-weight: 700;
      font-size: 36px;
      line-height: 44px;
      text-align: center;
      max-width: 570px;
      margin: 0 auto;
      margin-bottom: 24px;
    }

    &__button {
      margin-bottom: 43px;
    }

    &__subtitle {
      margin-bottom: 4px;
    }

    &__tel {
      margin-bottom: 24px;
    }

    &__text {
      font-size: 24px;
      line-height: 32px;
      margin-top: 24px;
    }
  }
}

.footer_fix {
  .footer {
    padding-top: 330px;
  }
}
